import React from "react";
import ReactMarkdown from "react-markdown";

class Markdown extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      data: [],
    };
  }
  
  componentDidMount() {
    this.readFile(this.props.path)
  }

  readFile = (path) => {
    fetch(path)
      .then(res => res.text())
      .then(text => {
        this.setState({
          data: text,
        });
      });
  }



  render() {
    return (
      <div style={{ padding: 24 }}>
        <ReactMarkdown> 
          {this.state.data}
        </ReactMarkdown>
      </div>
    );
  }
}

export default Markdown;